@import "https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&family=Roboto:wght@400;500;700&display=swap";
:root {
  --color-primary: #00acdc;
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  display: flex;
  position: relative;
}

.logo {
  margin: 0 50px;
  display: flex;
}

.nav {
  margin-right: 50px;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  margin: 0 0 0 20px;
  padding: 23px 0;
  font-family: roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.nav__link--no-margin {
  margin: 0;
}

.nav__list {
  justify-content: flex-end;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.is-active {
  color: var(--color-primary);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--color-primary);
  border-radius: 20%;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link:hover {
  color: var(--color-primary);
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
  margin: 0 4px 0 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 198px;
  height: 406px;
  font-family: Roboto, sans-serif;
  transition: transform .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  background-image: url("imac.202c52ff.jpeg");
  background-size: 160px 134px;
  width: 160px;
  height: 134px;
  margin: 32px 18px 40px 19px;
  display: block;
}

.card__name {
  color: #060b35;
  text-align: left;
  margin: 40px 16px 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover .card__name {
  color: #34568b;
}

.card__prodact-code {
  color: #616070;
  text-align: left;
  margin-top: 4px;
  margin-left: 16px;
  margin-right: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__stars-and-reviews {
  justify-content: space-between;
  margin: 16px;
  display: flex;
}

.card__reviews {
  text-align: right;
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
}

.card__price-and-text {
  color: #060b35;
  justify-content: space-between;
  margin: 24px 16px 16px;
  display: flex;
}

.card__price-text {
  text-align: left;
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price {
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.grid-area {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  display: grid;
}

@media (width >= 488px) {
  .grid-area {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .grid-area {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .grid-area {
    --column-count: 4;
  }
}

body {
  margin: 0;
}

.main {
  justify-content: center;
  padding: 50px 40px;
  display: flex;
}

